@import url(https://fonts.googleapis.com/css2?family=Nunito&display=swap);
*{margin:0;padding:0;box-sizing:inherit}html{font-size:62.5%;box-sizing:border-box}body{font-family:"Nunito", sans-serif;font-weight:400;height:100vh;color:#333;background-image:linear-gradient(to top left, #753682 0%, #bf2e34 100%);display:flex;align-items:center;justify-content:center}h2{padding-top:50px}h2:nth-of-type(2){padding-bottom:20px}.App{position:relative;width:50rem;height:100%;padding:5rem;background-color:rgba(255,255,255,0.35);-webkit-backdrop-filter:blur(200px);backdrop-filter:blur(200px);-webkit-filter:blur();filter:blur();box-shadow:0 3rem 5rem rgba(0,0,0,0.25);border-radius:9px;overflow:hidden}.notification{padding-top:50px;text-transform:uppercase;transition:-webkit-transform 5s;transition:transform 5s;transition:transform 5s, -webkit-transform 5s}.add{color:#8ade3d}.change{color:#f1eb2c}.delete{color:#f12c3e}.form__group{position:relative;padding:15px 0 0;margin-top:10px;width:100%}.form__field{width:100%;border:0;border-bottom:2px solid #333;outline:0;font-size:1.3rem;color:#333;padding:7px 0;background:transparent;transition:border-color 0.2s}.form__field::-webkit-input-placeholder{color:transparent}.form__field:-ms-input-placeholder{color:transparent}.form__field::placeholder{color:transparent}.form__field:-ms-input-placeholder ~ .form__label{font-size:1.3rem;cursor:text;top:20px}.form__field:placeholder-shown ~ .form__label{font-size:1.3rem;cursor:text;top:20px}.form__label{position:absolute;top:0;display:block;transition:0.2s;font-size:1rem;color:#333}.form__field:focus{padding-bottom:6px;font-weight:700;border-width:3px;border-image:linear-gradient(to right, #ac2a2e, #bf2e34);border-image-slice:1}.form__field:focus ~ .form__label{position:absolute;top:0;display:block;transition:0.2s;font-size:1rem;color:#ac2a2e;font-weight:700}.form__field:required,.form__field:invalid{box-shadow:none}.button{margin-top:20px;width:100%;height:40px;font-size:1.3rem;font-weight:500;color:#fff;background-color:#bf2e34;border:none;border-radius:45px;transition:all 0.3s ease 0s;cursor:pointer;outline:none}.button:hover{background-color:#bf2e34;box-shadow:0px 15px 20px rgba(0,0,0,0.25);color:#fff;-webkit-transform:translateY(-3px);transform:translateY(-3px)}.remove-button{background:none;color:inherit;border:none;padding:0;font:inherit;cursor:pointer;outline:inherit}.contact{font-size:1.3rem;margin-bottom:5px;display:flex;flex-direction:row;justify-content:space-between}@media only screen and (max-width: 600px){.App{width:34rem;margin:20px 0 0 0;padding:2rem}}

